import React from 'react';
import {NavLink } from 'react-router-dom';


function Navs(props){

  return (
    <nav className="navbar sticky-top navbar-expand-lg navbar-dark bg-dark" style={{marginBottom:'30px'}} >
    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarTogglerDemo01" aria-controls="navbarTogglerDemo01" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarTogglerDemo01">
      <NavLink className="navbar-brand"  to="/" >Vishwa Computers</NavLink>
      <ul className="navbar-nav mr-auto mt-2 mt-lg-0">
        <li className="nav-item ">
          <NavLink  className="nav-link" to="/" activeClassName="selected">Home <span className="sr-only">(current)</span></NavLink >
        </li>
        <li className="nav-item">
        <NavLink  className="nav-link" to="/Products">Products</NavLink >
        </li>
        <li className="nav-item">
        <NavLink  className="nav-link" to="/custom">Custom Build</NavLink >
        </li>
        <li className="nav-item">
        <NavLink  className="nav-link" to="/repair">Repair</NavLink >
        </li>
      </ul>
      <form className="form-inline my-2 my-lg-0">
        <input className="form-control mr-sm-2" type="search" placeholder="Search" aria-label="Search"/>
        <button className="btn btn-secondary my-2 my-sm-0" type="submit">Search</button>
      </form>
    </div>
  </nav>
    );

}

export default Navs;